* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

h1 {
	text-align: center;
	margin-bottom: 15px;
}

body {
	background-color: rgba(0, 0, 0, 0.9);
	color: white;
	font-family: Arial, Helvetica, sans-serif;
	display: flex;
	justify-content: center;
	min-height: 100vh;
	padding: 100px;
}

.board {
	width: 300px;
	height: 300px;
	border: 7px solid rgb(15, 107, 15);
	background: rgb(15, 107, 15);
	display: grid;
	grid-template: repeat(3, 1fr) / repeat(3, 1fr);
	gap: 7px;
}

.item {
	background: #186ea8;
	font-size: 10px;
	cursor: pointer;
  outline: none;
	font-size: 40px;
	border: 1px solid black;
	transition: all .5s ease;
}

.item:hover {
	opacity: .8;
}

.x {
	font-size: 20px;
	color: green;
}

.reset-button {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 15px auto 0;
	background: greenyellow;
	text-align: center;
	width: 150px;
	height: 30px;
	border: none;
	border-radius: 7px;
	cursor: pointer;
	font-weight: 700;
	transition: all .5s ease;
}

.reset-button:hover {
	opacity: .7;
}

.status {
	font-size: 20px;
	color: #cf8e15;
	text-align: center;
	margin-top: 15px;
}